.carousel {
  width: 800px;
  height: 560px;
}
.container-carousel {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

@media (max-width: 820px) {
  .carousel {
    width: 470px;
    height: 350px;
  }
}
@media (max-width: 390px) {
  .carousel {
    width: 400px;
    height: 290px;
    margin-top: -10px;
  }
  .container-carousel {
    margin-top: 0px;
    background-color: white;
  }
}
