@import url("https://fonts.googleapis.com/css2?family=Assistant:wght@200&family=Cormorant+Garamond:ital,wght@1,600&family=Lato:wght@300&family=Quicksand:wght@300&display=swap");

.digital-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  background-color: #80ced7;
  padding-top: 30px;
  padding-bottom: 30px;
}

.offer-img {
  width: 600px;
}

p {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 600px;
  font-family: "Assistant", sans-serif;
  font-size: 1.4rem;
}

@media only screen and (max-width: 820px) {
  .digital-container {
    flex-direction: column;
  }
  .offer-img {
    width: 400px;
  }
  p {
    width: 400px;
  }
}
@media only screen and (max-width: 390px) {
  .digital-container {
    flex-direction: column;
  }
  .offer-img {
    width: 300px;
  }
  p {
    width: 300px;
    font-size: 1.2rem;
    margin-bottom: 30px;
    margin-top: 20px;
  }
}
